import React from "react";
import classNames from "../PracticeBoard.module.css";

const CarryCell = ({ p }) => {
  const cellSizeStyle = () => {
    return {
      width: `${p.gs * 60}px`,
      height: `${p.gs * 40}px`,
      lineHeight: `${p.gs * 40}px`,
    };
  };
  return (
    <div
      className={classNames.carryInput}
      style={{ borderRight: p.gridToggle(), ...cellSizeStyle() }}
    >
      {p.c ? <h2 style={{ fontSize: `${p.gs * 26}px` }}>{p.c}</h2> : null}
    </div>
  );
};

export default CarryCell;
