import React from "react";
import { connect } from "react-redux";
import classNames from "../../containers/NumberBoard/Board.module.css";

const Row = (props) => {
  const cellSize = props.gridCellSizes.learn;
  const cellSizeStyle = () => {
    return {
      width: `${cellSize * 60}px`,
      height: `${cellSize * 40}px`,
      lineHeight: `${cellSize * 40}px`,
    };
  };
  let classes;
  if (props.grid) {
    classes = `${classNames.carry} ${classNames.c3} ${classNames.brc}`;
  } else {
    classes = `${classNames.carry} ${classNames.c3}`;
  }
  return (
    <>
      <div className={`${classNames.row}`}>
        <div className={classes} style={cellSizeStyle()}>
          <h2 style={{ fontSize: `${cellSize * 26}px` }}>{null}</h2>
        </div>
        <div className={classes} style={cellSizeStyle()}>
          <h2 style={{ fontSize: `${cellSize * 26}px` }}>{null}</h2>
        </div>
        <div className={classes} style={cellSizeStyle()}>
          {props.n[2] ? (
            <h2
              style={{
                backgroundColor: "#ffffc0",
                fontSize: `${cellSize * 26}px`,
              }}
            >
              {props.n[2]}
            </h2>
          ) : null}
        </div>
        <div className={classes} style={cellSizeStyle()}>
          {props.n[1] ? (
            <h2
              style={{
                backgroundColor: "#ffffc0",
                fontSize: `${cellSize * 26}px`,
              }}
            >
              {props.n[1]}
            </h2>
          ) : null}
        </div>
        <div className={classes} style={cellSizeStyle()}>
          {props.n[0] ? (
            <h2
              style={{
                backgroundColor: "#ffffc0",
                fontSize: `${cellSize * 26}px`,
              }}
            >
              {props.n[0]}
            </h2>
          ) : null}
        </div>
        <div className={classes} style={cellSizeStyle()}>
          {null}
        </div>
      </div>
    </>
  );
};

const mapStatetoProps = (state) => {
  return {
    gridCellSizes: state.menu.gridCellSizes,
  };
};
export default connect(mapStatetoProps)(Row);
