import React from "react";
import { connect } from "react-redux";
import classNames from "../../containers/NumberBoard/Board.module.css";

const Row = (props) => {
  const cellSize = props.gridCellSizes.learn;
  const cellSizeStyle = () => {
    return {
      width: `${cellSize * 60}px`,
      height: `${cellSize * 60}px`,
      lineHeight: `${cellSize * 60}px`,
    };
  };
  const digit = props.n ? props.n : [];

  let classes;
  switch (props.color) {
    case "r1":
      if (props.grid) {
        classes = `${classNames.results} ${classNames.r1} ${classNames.brf}`;
      } else {
        classes = `${classNames.results} ${classNames.r1}`;
      }
      break;
    case "r2":
      if (props.grid) {
        classes = `${classNames.results} ${classNames.r2} ${classNames.brs}`;
      } else {
        classes = `${classNames.results} ${classNames.r2}`;
      }
      break;
    case "r3":
      if (props.grid) {
        classes = `${classNames.results} ${classNames.r3} ${classNames.brt}`;
      } else {
        classes = `${classNames.results} ${classNames.r3}`;
      }
      break;
    case "r4":
      if (props.grid) {
        classes = `${classNames.results} ${classNames.r4} ${classNames.brl} ${classNames.btlr}`;
      } else {
        classes = `${classNames.results} ${classNames.r4} ${classNames.btlr}`;
      }
      break;

    default:
      break;
  }
  let zeroColor = "#D9D9D9";
  if (window.innerHeight < 420) {
    zeroColor = "black";
  }
  return (
    // #ffffc0
    <div className={classNames.row}>
      <div
        className={classes}
        style={{
          backgroundColor: `${
            props.blank || props.blankH ? "transparent" : null
          }`,
          ...cellSizeStyle(),
        }}
      >
        <h1 style={{ fontSize: `${cellSize * 40}px` }}>{digit[5]}</h1>
      </div>
      <div
        className={classes}
        style={{
          backgroundColor: `${props.blank ? "transparent" : null}`,
          ...cellSizeStyle(),
        }}
      >
        <h1 style={{ fontSize: `${cellSize * 40}px` }}>{digit[4]}</h1>
      </div>
      <div className={classes} style={cellSizeStyle()}>
        <h1 style={{ fontSize: `${cellSize * 40}px` }}>{digit[3]}</h1>
      </div>
      <div className={classes} style={cellSizeStyle()}>
        <h1 style={{ fontSize: `${cellSize * 40}px` }}>{digit[2]}</h1>
      </div>
      <div
        className={classes}
        style={{
          backgroundColor: `${props.zero ? "transparent" : null}`,
          color: `${props.zero ? zeroColor : null}`,
          ...cellSizeStyle(),
        }}
      >
        <h1 style={{ fontSize: `${cellSize * 40}px` }}>{digit[1]}</h1>
      </div>
      <div
        className={classes}
        style={{
          backgroundColor: `${
            props.zero || props.zeroO ? "transparent" : null
          }`,
          color: `${props.zero || props.zeroO ? zeroColor : null}`,
          ...cellSizeStyle(),
        }}
      >
        <h1 style={{ fontSize: `${cellSize * 40}px` }}>{digit[0]}</h1>
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    grid: state.menu.grid,
    gridCellSizes: state.menu.gridCellSizes,
  };
};

export default connect(mapStatetoProps)(Row);
