import React, { useEffect } from "react";
import { connect } from "react-redux";
import * as reduxFunctions from "../../store/actions/index";
import classNames from "./Bar.module.css";

const Bar = (props) => {
  let grid;
  if (props.modes.learn) {
    grid = "learn";
  } else if (props.modes.practice) {
    grid = "practice";
  } else if (props.modes.exam) {
    grid = "exam";
  }
  const slideToggle = (e) => {
    e.target.classList.toggle(`${classNames.dropDownTriggerActive}`);
    if (e.target.nextSibling.style.display === "block") {
      e.target.nextSibling.classList.remove(`${classNames.dropDownActive}`);
      setTimeout(() => {
        e.target.nextSibling.style.display = "none";
      }, 200);
    } else {
      e.target.nextSibling.style.display = "block";
      setTimeout(() => {
        e.target.nextSibling.classList.add(`${classNames.dropDownActive}`);
      }, 10);
    }
  };
  const btnActive = function () {
    this.classList.toggle(`${classNames.active}`);
  };
  const resizeGridBtns = () => {
    document
      .querySelector(`.${classNames.cell_size_control}`)
      .classList.toggle(`${classNames.onView}`);
  };
  useEffect(() => {
    document.querySelectorAll(`#dropDown button`).forEach((cur) => {
      cur.addEventListener("click", btnActive);
    });
    return () => {
      document.querySelectorAll(`#dropDown button`).forEach((cur) => {
        cur.removeEventListener("click", btnActive);
      });
    };
  });
  return (
    <div
      className={classNames.container}
      style={props.sideMenuToggle ? { right: "0px" } : { right: "-100%" }}
      id="sidebar"
    >
      <div className={classNames.dropDownContainer}>
        <div
          className={classNames.dropDownTrigger}
          onClick={(e) => slideToggle(e)}
        >
          Preferences
        </div>
        <div className={classNames.dropDown} id="dropDown">
          <button onClick={props.pheadersToggle}>
            Toggle placement headers
          </button>
          <button onClick={props.gridToggle}>Toggle grid</button>

          {props.modes.learn ? (
            <button onClick={props.carryToggle}>Toggle carry</button>
          ) : null}

          <button onClick={resizeGridBtns}>Resize cells</button>

          <div className={classNames.cell_size_control}>
            {/* <input type="range" min={0} max={100} /> */}
            <div
              className={classNames.resize_grid_btn}
              onClick={() => {
                props.decreaseGridSize(grid);
              }}
            >
              <i className="fas fa-minus"></i>
            </div>
            <div
              className={classNames.resize_grid_btn}
              onClick={() => {
                props.increaseGridSize(grid);
              }}
            >
              <i className="fas fa-plus"></i>
            </div>
          </div>
        </div>
      </div>
      <div className={classNames.dropDownContainer}>
        <div
          className={classNames.dropDownTrigger}
          onClick={(e) => slideToggle(e)}
        >
          Modes
        </div>
        <div className={classNames.dropDown}>
          <button
            className={props.modes.learn ? classNames.active : null}
            onClick={props.boardSwitch.bind(this, "learn")}
          >
            Learn
          </button>
          <button
            className={props.modes.practice ? classNames.active : null}
            onClick={props.boardSwitch.bind(this, "practice")}
          >
            Practice
          </button>
          <button
            className={props.modes.exam ? classNames.active : null}
            onClick={props.boardSwitch.bind(this, "exam")}
          >
            Exam
          </button>
        </div>
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    sideMenuToggle: state.menu.sideMenuToggle,
    modes: state.menu.modes,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    pheadersToggle: () => dispatch(reduxFunctions.pHeadersToggle()),
    gridToggle: () => dispatch(reduxFunctions.gridToggle()),
    carryToggle: () => dispatch(reduxFunctions.carryToggle()),
    boardSwitch: (mod) => dispatch(reduxFunctions.boardSwitch(mod)),
    increaseGridSize: (g) => dispatch(reduxFunctions.increaseGridSize(g)),
    decreaseGridSize: (g) => dispatch(reduxFunctions.decreaseGridSize(g)),
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(Bar);
