import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import classNames from "../../containers/NumberBoard/Board.module.css";

const Row = (props) => {
  const placementHeadersCount = [0, 1, 2, 3, 4, 5];
  const [cellSize, setCellSize] = useState(1);
  const [placementHeaders, setPlacementHeaders] = useState([
    "100T",
    "10T",
    "1T",
    "100",
    "10",
    "1",
  ]);
  const placementHeadersMouseDown = () => {
    setPlacementHeaders([
      "Hundred Thousands",
      "Ten Thousands",
      "Thousands",
      "Hundreds",
      "Tens",
      "Ones",
    ]);
  };
  const placementHeadersMouseUp = () => {
    setPlacementHeaders(["100T", "10T", "1T", "100", "10", "1"]);
  };
  useEffect(() => {
    if (props.modes.learn) {
      setCellSize(props.gridCellSizes.learn);
    } else if (props.modes.practice) {
      setCellSize(props.gridCellSizes.practice);
    }
  }, [props]);
  return (
    <div className={`${classNames.row} ${classNames.rowHead}`}>
      {placementHeadersCount.map((c, i) => {
        return (
          <div
            key={i}
            className={`${classNames.title} ${classNames.brt}`}
            style={{
              width: `${cellSize * 60}px`,
              lineHeight: `${cellSize * 60}px`,
            }}
          >
            <h4
              onMouseDown={placementHeadersMouseDown}
              onMouseUp={placementHeadersMouseUp}
              onTouchStart={placementHeadersMouseDown}
              onTouchEnd={placementHeadersMouseUp}
              style={{ fontSize: `${cellSize * 18}px` }}
            >
              {placementHeaders[c]}
            </h4>
          </div>
        );
      })}
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    gridCellSizes: state.menu.gridCellSizes,
    modes: state.menu.modes,
  };
};
export default connect(mapStatetoProps)(Row);
