import React from "react";
import classNames from "../PracticeBoard.module.css";

const InputCell = ({ p }) => {
  const cellSizeStyle = () => {
    return {
      width: `${p.gs * 60}px`,
      height: `${p.gs * 60}px`,
      lineHeight: `${p.gs * 60}px`,
    };
  };
  return (
    <div
      className={classNames.numberInput}
      style={{
        backgroundColor: p.f,
        borderRight: p.gridToggle(),
        ...cellSizeStyle(),
      }}
    >
      {p.c ? <h1 style={{ fontSize: `${p.gs * 40}px` }}>{p.c}</h1> : null}
    </div>
  );
};

export default InputCell;
