import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import swal from "sweetalert";
import classNames from "./PracticeBoard.module.css";
import * as reduxFunctions from "../../store/actions/index";

import Header from "../../components/BoardHeaderRow/Row";
import Row from "../../components/PracticeBoardResultsRow/Row";
import CarryCell from "./components/CarryCell";
import InputCell from "./components/InputCell";

const PracticeBoard = (props) => {
  const cellSize = props.gridCellSizes.practice;

  const [focusingInput, setFocusingInput] = useState({ t: {}, b: {} });
  const [carryInput, setCarryInput] = useState({});

  const carryFocus = (e) => {
    const carriesCell = [
      ...document.getElementsByClassName(e.target.getAttribute("class")),
    ];
    const indexOfCarry = carriesCell.indexOf(e.target);
    if (props.inputDisable) {
      if (props.carryClick) {
        carriesCell.forEach((c) => (c.style.backgroundColor = "transparent"));
        if (indexOfCarry === props.correctCarryCell) {
          e.target.style.backgroundColor = "#ffffc0";
          props.messagePractice(
            "You got the carry cell! Now enter the value of it."
          );
          props.pcf(true);
        } else {
          e.target.style.backgroundColor = "#ff867c";
          props.messagePractice("Sorry try again");
          props.pcf(false);
        }
      } else {
        swal(
          "Wrong Action!",
          "You have already chosen the right carry cell.",
          "error"
        );
      }
    } else {
      swal(
        "Wrong Action!",
        "Please end digits and start the mutliplication first.",
        "error"
      );
    }
  };

  useEffect(() => {
    setFocusingInput({ t: {}, b: {} });
    if (props.addZero) {
      setFocusingInput({ t: {}, b: {} });
    } else {
      setFocusingInput({
        t: {
          [props.t]: true,
        },
        b: {
          [props.b]: true,
        },
      });
    }
    setCarryInput({});
    setCarryInput({ [props.c]: props.carry });
  }, [props]);

  let headerForMobile;
  if (window.innerWidth > 924) {
    headerForMobile = props.pheader ? <Header /> : null;
  } else {
    headerForMobile = props.pheaderMobile ? <Header /> : null;
  }

  const gridToggle = () => {
    if (props.grid) {
      return "1px solid rgba(0, 0, 0, 0.1)";
    } else {
      return "none";
    }
  };

  return (
    <div className={classNames.container}>
      <div className={classNames.inner}>
        {headerForMobile}
        <div
          className={classNames.carryRow}
          onClick={(e) => {
            carryFocus(e);
          }}
          style={{ borderBottom: gridToggle() }}
          id="carryInputContainer"
        >
          <CarryCell p={{ gridToggle: gridToggle, gs: cellSize }} />
          <CarryCell p={{ gridToggle: gridToggle, gs: cellSize }} />
          <CarryCell
            p={{
              gridToggle: gridToggle,
              c: carryInput[2],
              gs: cellSize,
            }}
          />
          <CarryCell
            p={{
              gridToggle: gridToggle,
              c: carryInput[1],
              gs: cellSize,
            }}
          />
          <CarryCell
            p={{
              gridToggle: gridToggle,
              c: carryInput[0],
              gs: cellSize,
            }}
          />
          <CarryCell p={{ gridToggle: gridToggle, gs: cellSize }} />
        </div>
        {/* Top Row Start */}
        <div
          className={classNames.numberRow}
          style={{ borderBottom: gridToggle() }}
        >
          <InputCell p={{ gridToggle: gridToggle, gs: cellSize }} />
          <InputCell p={{ gridToggle: gridToggle, gs: cellSize }} />
          <InputCell p={{ gridToggle: gridToggle, gs: cellSize }} />
          <InputCell
            p={{
              gridToggle: gridToggle,
              f: focusingInput.t[0] ? "yellow" : null,
              c: props.tR[0],
              gs: cellSize,
            }}
          />
          <InputCell
            p={{
              gridToggle: gridToggle,
              f: focusingInput.t[1] ? "yellow" : null,
              c: props.tR[1],
              gs: cellSize,
            }}
          />
          <InputCell
            p={{
              gridToggle: gridToggle,
              f: focusingInput.t[2] ? "yellow" : null,
              c: props.tR[2],
              gs: cellSize,
            }}
          />
        </div>
        {/* Top Row End */}
        {/* Bottom Row Start */}
        <div className={`${classNames.numberRow} ${classNames.redBorder}`}>
          <InputCell p={{ gridToggle: gridToggle, c: "X", gs: cellSize }} />
          <InputCell p={{ gridToggle: gridToggle, gs: cellSize }} />
          <InputCell p={{ gridToggle: gridToggle, gs: cellSize }} />
          <InputCell
            p={{
              gridToggle: gridToggle,
              f: focusingInput.b[0] ? "yellow" : null,
              c: props.bR[0],
              gs: cellSize,
            }}
          />
          <InputCell
            p={{
              gridToggle: gridToggle,
              f: focusingInput.b[1] ? "yellow" : null,
              c: props.bR[1],
              gs: cellSize,
            }}
          />
          <InputCell
            p={{
              gridToggle: gridToggle,
              f: focusingInput.b[2] ? "yellow" : null,
              c: props.bR[2],
              gs: cellSize,
            }}
          />
        </div>
        {/* Bottom Row End */}
        {/* Result Row 1 Start */}
        <Row
          allProps={{
            rowIndex: 0,
            correctResultRow: props.correctResultRow,
            messagePractice: props.messagePractice,
            correctResultCell: props.correctResultCell,
            result: props.result[0],
            started: props.inputDisable,
            rc: props.resultClick,
            prf: props.prf,
            gridToggle: gridToggle,
            gs: cellSize,
          }}
        />
        {/* Result Row 1 End */}

        {/* Result Row 2 Start */}
        <Row
          allProps={{
            rowIndex: 1,
            correctResultRow: props.correctResultRow,
            messagePractice: props.messagePractice,
            correctResultCell: props.correctResultCell,
            result: props.result[1],
            started: props.inputDisable,
            rc: props.resultClick,
            prf: props.prf,
            gridToggle: gridToggle,
            gs: cellSize,
          }}
        />
        {/* Result Row 2 End */}

        {/* Result Row 3 Start */}
        <Row
          allProps={{
            yellowBorder: true,
            rowIndex: 2,
            correctResultRow: props.correctResultRow,
            messagePractice: props.messagePractice,
            correctResultCell: props.correctResultCell,
            result: props.result[2],
            started: props.inputDisable,
            rc: props.resultClick,
            prf: props.prf,
            gridToggle: gridToggle,
            gs: cellSize,
          }}
        />
        {/* Result Row 3 End */}

        {/* Result Row 4 Start */}
        <Row
          allProps={{
            rowIndex: 3,
            correctResultRow: props.correctResultRow,
            messagePractice: props.messagePractice,
            correctResultCell: props.correctResultCell,
            result: props.result[3],
            started: props.inputDisable,
            rc: props.resultClick,
            prf: props.prf,
            gridToggle: gridToggle,
            gs: cellSize,
          }}
        />
        {/* Result Row 4 End */}
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    tR: state.number.topRow,
    bR: state.number.bottomRow,
    t: state.pMode.tr,
    b: state.pMode.br,
    c: state.pMode.c,
    p: state.pMode.p,
    carry: state.pMode.carry,
    result: state.pMode.result,
    showResult: state.pMode.selectResult,
    correctCarryCell: state.pMode.correctCarryCell,
    correctResultCell: state.pMode.correctResultCell,
    correctResultRow: state.pMode.correctResultRow,
    btnHide: state.pMode.btnHide,
    inputDisable: state.number.inputDisable,
    carryClick: state.pMode.carryClick,
    resultClick: state.pMode.resultClick,
    addZero: state.pMode.addZero,
    finalResultValues: state.pMode.finalResultValues,
    pheader: state.menu.pheader,
    pheaderMobile: state.menu.pheaderMobile,
    grid: state.menu.grid,
    // lc: state.menu.carry,
    gridCellSizes: state.menu.gridCellSizes,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    messagePractice: (m) => dispatch(reduxFunctions.messagePractice(m)),
    pcf: (v) => dispatch(reduxFunctions.pcf(v)),
    prf: (v) => dispatch(reduxFunctions.prf(v)),
  };
};

export default connect(mapStatetoProps, mapDispatchtoProps)(PracticeBoard);
