import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import * as reduxFunctions from "../../store/actions/index";
import classNames from "./Header.module.css";
import logo from "../../assets/logo.svg";
import { NavLink } from "react-router-dom";
import Logout from "../../store/global/logout";

const Header = (props) => {
  const [icon, setIcon] = useState(<i className="fas fa-bars"></i>);
  const [profileImage, setProfileImage] = useState("");

  const { sideMenuToggle } = props;

  useEffect(() => {
    setProfileImage(JSON.parse(localStorage.getItem("user")).image);
    if (!sideMenuToggle) {
      setIcon(<i className="fas fa-bars"></i>);
    } else {
      setTimeout(() => {
        setIcon(<i className="fas fa-times"></i>);
      }, 300);
    }
  }, [sideMenuToggle]);

  const sideMenuToggleClick = () => {
    props.sideMenuToggleFn();
  };

  const logout = () => {
    Logout(props.h);
  };
  return (
    <div className={classNames.header}>
      <div className={classNames.logo}>
        <a href="!#">
          <img src={logo} alt="Logo" />
          Multi Plus
        </a>
      </div>
      <div className={classNames.header_icons}>
        <div className={classNames.profile_image}>
          <NavLink to="/profile">
            <img src={profileImage} alt="Profile" />
          </NavLink>
        </div>
        <div className={classNames.logout}>
          <button onClick={logout}>
            <i className="fas fa-sign-out-alt"></i>
          </button>
        </div>
        <div
          className={classNames.ico}
          onClick={() => {
            sideMenuToggleClick();
          }}
          id="sideBarTrigger"
        >
          {icon}
        </div>
      </div>
    </div>
  );
};
const mapStatetoProps = (state) => {
  return {
    sideMenuToggle: state.menu.sideMenuToggle,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    sideMenuToggleFn: () => dispatch(reduxFunctions.sideMenuToggle()),
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(Header);
